import React,{useState} from "react"
import {Link} from "react-router-dom"
import Input from "../components/Input"
import {User} from "../services/context"
import Button from "../components/Button"
import Container from "../components/Container"
import {FaEnvelope, FaUser,  FaKey} from "react-icons/fa"
import Logo from "../images/logo-light.svg";

const Login=()=>{

 let [userData, setUserData]=useState({
   email:"", 
   password:"", 
 })
 const  submitForm=(e)=>{
   e.preventDefault()
   setUserData(userData = {
     email:	e.target.elements.item(0).value, 
     password: e.target.elements.item(1).value 
   })
  
   //alert(JSON.stringify(e))
   
fetch("https://book--lib.herokuapp.com/login", {
  method:"POST", 
headers: {
  "Content-type": "application/json; charset=UTF-8"
    }, 
  body:JSON.stringify(userData)
})
.then(res=> {
  return res.json()
})
.then(data=>{
  if(data.status >=200 && data.status <=201){
    window.location ="/"
  }
  alert(data.msg)
})
.catch(err=> alert(err))

 }
  return(
    <User.Provider value={{userData}}  >
<Container>
      <div class="d-flex justify-content-center py-5">
        <img class="logo" width="100" src={Logo} alt="Book Library " />
      </div>
      
      
    <div class="jumbotron">
          <h3 class="fs-3 fw-bold ">Login</h3>
          <p class="">
            Login with your credential correctly. 
          </p>
        </div>




    <form onSubmit={submitForm} class="py-3" >   <Input type="email" placeholder="Enter Email"   icon={<FaEnvelope/>}  />
   
   
   <Input type="password"  placeholder="Enter Password "   icon={<FaKey/>}  />
   
   
   
      <div class="form-group mb-3">
          <label for="">
Not yet a user? <span class ="fw-bold"><Link to="/signup">
    SignUp 
    </Link>
    </span>
          </label>
        </div>

   
   
   <Button type="submit" class="btn-primary p-2 border-0 w-100 " >
   Submit
   </Button>
   </form>
    </Container>
</User.Provider>
    )
}

export default Login 