const Button=( props )=>{
  return (
    <button disabled={props.loading && "true"}  class={`" btn" ${props.loading &&"disabled"} `}  {...props}>
    <span class="text">{props.children}</span>
    {
  props.loading && <span class="loading spinner-border spinner-border-sm mx-2"></span>
    }
    </button>
    )
}
Button.defaultProps={
  loading:false
}

export default Button