import React, {useState, useEffect, Fragment} from "react"
import Header from "../components/Header"
import BookCard from "../components/BookCard"
import Loader from "../components/Loader"
import Container from "../components/Container"
import Nav from "../components/Nav"

const Borrows = ()=>{
let [userData, setUserData ]=useState({})
let [books, setBooks]=useState();
  
const onReturn=(id, cb)=>{
fetch("https://book--lib.herokuapp.com/return-book/"+id)
.then(res=>res.json())  
.then(data=> cb(null, data)) 
.catch(err=> cb(err, null)) 
}


  
  React.useEffect(()=>{

fetch("https://book--lib.herokuapp.com/borrowed-book") 
 .then(res=>res.json())
.then(data=>{
  setBooks(books = data.data )
})

fetch("https://book--lib.herokuapp.com/profile") 
 .then(res=>res.json())
.then(data=>{
  if(data.status!==200)  {
    window.location ="/login"
    alert("You need to login first ")
  }else{
// alert(JSON.stringify(books))
  setUserData(...data.data)
  } 
})
.catch(err=> (err))

 }, [])
  return (
    <Container>
    <Header username={userData.username} />
    <Nav  />
    <h3 class="my-3">Book Borrowed </h3>
<div class="p-5">
    {
     books? (
    books.map(book=>{
      return(
      <BookCard returnbook={onReturn}  key={book._id} options={{
        author:book.author, 
        id:book._id,
     //   username:"username", 
        title :book.name
    }  } />
      )
    })
     ): <Loader />
    }
    
    </div>
    </Container>
    )
}

export default Borrows