import React,{useState} from "react"
import {Link} from "react-router-dom"
import Input from "../components/Input"
import Button from "../components/Button"
import Container from "../components/Container"
import {FaEnvelope, FaUser,  FaKey} from "react-icons/fa"
import Logo from "../images/logo-light.svg";

const SignUp=()=>{
 let [email, setEmail]=useState("")
 let [username, setUsername]=useState("")
 let [password, setPassword]=useState("")
 let [userData, setUserData]=useState({
   username:"",
   email:"", 
   password:"", 
 })
 const  submitForm=(e)=>{
   e.preventDefault()
   setUserData(userData = {
     email:	e.target.elements.item(0).value, 
     username:	e.target.elements.item(1).value, 
     password: e.target.elements.item(2).value 
   })
  
   //alert(JSON.stringify(e))
   
fetch("https://book--lib.herokuapp.com/signup", {
  method:"POST", 
headers: {
  "Content-type": "application/json; charset=UTF-8"
    }, 
  body:JSON.stringify(userData)
})
.then(res=> {
  return res.json()
})
.then(data=>{
  if(data.status >=200 && data.status <=201){
    window.location ="/"
  }
  alert(data.msg)
})
.catch(err=> alert(err))

 }
  return(
    <Container>
      <div class="d-flex justify-content-center py-5">
        <img class="logo" width="100" src={Logo} alt="Book Library " />
      </div>
      
      
    <div class="jumbotron">
          <h3 class="fs-3 fw-bold ">Sign Up</h3>
          <p class="">
            Create a new Account.
          </p>
        </div>




    <form onSubmit={submitForm} class="py-3" >
   <Input type="email" onChange={(e)=>{
     setEmail(email=e.target.value)
   }}  placeholder="Enter Email"   icon={<FaEnvelope/>}  />
   
   <Input type="username" onChange={(e)=>{
     setUsername(username=e.target.value)
   }}  placeholder="Enter Username"   icon={<FaUser/>}  />
   
   <Input type="password" onChange={(e)=>{
     setPassword(password=e.target.value)
   }}  placeholder="Enter Password "   icon={<FaKey/>}  />
   
   
   
      <div class="form-group mb-3">
          <label for="">
    Already a user? <span class ="fw-bold"><Link to="/login">
    Login 
    </Link>
    </span>
          </label>
        </div>

   
   
   <Button type="submit" class="btn-primary p-2 border-0 w-100 " >
   Submit
   </Button>
   </form>
    </Container>
    )
}

export default SignUp