import React, {useContext, useState } from "react"

import Container from "../components/Container"
import BookCard from "../components/BookCard"
import Header from "../components/Header"
import Logout from "../components/Logout"
import Loader from "../components/Loader"
import Nav from "../components/Nav"
import Footer from "../components/Bottom"
import {User} from "../services/context"
import {FaEnvelope} from "react-icons/fa"

const Homepage=()=>{
  let [userData, setUserData ]=useState({})
let [books, setBooks]=useState();
  
const onBorrow=(id, cb)=>{
fetch("https://book--lib.herokuapp.com/borrow-book/"+id, {
  method:"POST", 
})
.then(res=>res.json())  
.then(data=> cb(null, data)) 
.catch(err=> cb(err, null)) 
}


const onDelBook=(id, cb)=>{
fetch("https://book--lib.herokuapp.com/del-book/"+id, {
  method:"POST"
})
.then(res=>res.json())  
.then(data=> cb(null, data)) 
.catch(err=> cb(err, null)) 
}
  
  React.useEffect(()=>{

fetch("https://book--lib.herokuapp.com/books") 
 .then(res=>res.json())
.then(data=>{
  setBooks(books = data.data )
})

fetch("https://book--lib.herokuapp.com/profile") 
 .then(res=>res.json())
.then(data=>{
  if(data.status!==200)  {
    window.location ="/login"
    alert("You need to login first ")
  }else{
// alert(JSON.stringify(books))
  setUserData(...data.data)
  } 
})
.catch(err=> (err))

 }, [])
  
// alert(JSON.stringify (userData )
  return(
    <Container>
    <Header username={userData.username} />
    <Nav books={ "0"  }  />
    
    <div class="p-5">
    {
     books? (
    books.map(book=>{
      return(
      <BookCard borrow={onBorrow} del={onDelBook}   key={book._id} options={{
        author:book.author, 
        desc :book.description, 
        id:book._id,
     //   username:"username", 
        title :book.name
    }  } />
      )
    })
     ): <Loader />
    }
    
    </div>
    <Logout />
    <Footer  />
    </Container>
   )
}

export default Homepage 