import {Fragment} from "react"
import {Link} from "react-router-dom"
import {FaBook} from "react-icons/fa"
const BookCard=({options, borrow, returnbook, del })=>{
  return (
  
<div class="card p-5 text-center m-2 d-flex justify-content-center align-items-center list-item shadow bg-black">
    <span class="icon p-5 my-3 d-flex justify-content-center align-items-center bg-primary fs-3 rounded-circle bg-dark shadow-sm ">
      <FaBook />
    </span>
    <span class="fs-2 my-2"> {options.title} </span>
    <span class="fs-5 my-2"> <span class="fw-bold">{options.desc}</span> </span>
    <span class="fs-tiny my-2">  <span class="fw-bold">Author: {options.author} </span> </span>
    
  <div class="btn-group p-2">
   <Link to={"/book/"+options.id} >
   <button class="btn btn-info">Preview</button>
   </Link>
  { returnbook ?   <button class="btn btn-success" onClick={()=>{
    returnbook(options.id, (err, data )=>{
      if(err){
        alert("An error occurred")
      }else{
        alert(data.msg)
      }
    })
  }
 } >Return book</button> : 
  <Fragment>
  {
    borrow && <button class="btn btn-success" onClick={()=>{
     borrow(options.id, (err, data)=>{
     if(err){
       alert("err"+err)
     }else{
       
       alert(data.msg)
     }
     })
   }} >Borrow</button>
  }
   
   
   <button class="btn btn-danger" onClick={()=>{
     del(options.id, (err, data)=>{
     if(err){
       alert("err"+err)
     }else{
       
       alert(data.msg)
     }
     })
   }} >Delete</button>
   </Fragment>
  } 
  </div>
  
  </div>
  
  
    )
}

export default BookCard