import AOS from "aos"
import {BrowserRouter,Switch,  Route} from 'react-router-dom';
import {User} from "./services/context"
import isAuth from "./services/auth"
import Preview from "./pages/Preview"
import MyBooks from "./pages/MyBooks"
import Homepage from "./pages/Homepage"
import Borrow from "./pages/Borrow"
import SignUp from "./pages/SignUp"
import Login from "./pages/Login"
import "aos/dist/aos.css"
import './b.css';
try {
  AOS.init({
    mirror :true
  })
} catch (e) {
  throw e
}

const App=()=>{
  return (
    <User.Provider value={isAuth}  >
  <BrowserRouter>
  <Switch>
    
  <Route exact path={"/"} >
  <Homepage/>
  </Route>
  
  <Route path={"/my-books"} >
  <MyBooks />
  </Route>
  
  
  
  <Route path={"/borrowed"} >
  <Borrow />
  </Route>
  
  
  <Route path={"/signup"} >
  <SignUp />
  </Route>
  
  
  <Route path={"/login"} >
  <Login />
  </Route>
  
  <Route path={"/book/:id"} >
  <Preview />
  </Route>
  
  
  
  <Route render={()=>{
return(
  '404 Page'
)
  }} />
  </Switch>
  </BrowserRouter>
  </User.Provider>
  );
}

export default App;
