import {useState} from "react"
import {useParams, Link } from "react-router-dom"
import Header from "../components/Header"
import Container from "../components/Container"
const Preview =()=>{
  const {id} =useParams()
  const [books, setBooks ] = useState({})
  fetch("https://book--lib.herokuapp.com/books/id/"+id)
 .then(res=> res.json() )
.then(data=>{
  setBooks(data.data[0]);
//  alert(JSON.stringify(books))
})
.catch(err=>  alert(err))
  return (
    <Container>
    
    <div class="p-5">
    <h1 class="display-3 fw-bold my-2">{books.name}</h1>
    <h5 class="">Author: {books.author}</h5>
    <h6 class="my-2">Id: {books._id}</h6>
    <h5 class="my-2">
    <p class="mb-0">Description: </p>
    {books.description}</h5>
    
    <div class="p-4 l"style={{border:"2px solid blue "}} >
    <p>{books.content}</p>
    </div>
    </div>
    </Container>
    )
}

export default Preview