import React, {Fragment, useEffect, useState } from "react"
import Header from "../components/Header"
import Loader from "../components/Loader"
import Container from "../components/Container"
import BookCard from "../components/BookCard"
import Nav from "../components/Nav"

const MyBooks=()=>{
let [userData, setUserData ]=useState({})
let [books, setBooks]=useState();

const onDelBook=(id, cb)=>{
fetch("https://book--lib.herokuapp.com/del-book/"+id, {
  method:"POST"
})
.then(res=>res.json())  
.then(data=> cb(null, data)) 
.catch(err=> cb(err, null)) 
}

  
  React.useEffect(()=>{

fetch("https://book--lib.herokuapp.com/my-books") 
 .then(res=>res.json())
.then(data=>{
  setBooks(books = data.data )
})

fetch("https://book--lib.herokuapp.com/profile") 
 .then(res=>res.json())
.then(data=>{
  if(data.status!==200)  {
    window.location ="/login"
    alert("You need to login first ")
  }else{
// alert(JSON.stringify(books))
  setUserData(...data.data)
  } 
})
.catch(err=> (err))

 }, [])
  
  return (
    <Fragment>
    
    <Container>
    
    <Header username={userData.username} />
    <Nav books={"0"} />
   <h3 class="my-3"> My Books </h3>
    <div class="p-5">
    {
     books? (
    books.map(book=>{
      return(
      <BookCard key={book._id} del={onDelBook}  options={{
        author:book.author, 
        id:book._id,
        desc:book.description , 
        title :book.name
    }  } />
      )
    })
     ): <Loader />
    }
    </div>
    </Container>
    
    
    </Fragment>
    
    )
}

export default MyBooks 